import React, { useEffect, useState } from 'react';
import { Grid, Box, Skeleton, Container, Paper, Typography } from '@mui/material';
import DashboardLayout from '../components/layouts/DashboardLayout';
import StatCard from '../components/dashboard/StatCard';
import SalesChart from '../components/dashboard/SalesChart';
import TopProductsChart from '../components/dashboard/TopProductsChart';
import OrderStatusChart from '../components/dashboard/OrderStatusChart';
import { fetchSalesData, fetchStatistics, fetchTopProducts, fetchOrderStatus } from '../utils/api';
import { ShoppingCart, AttachMoney, People, Inventory } from '@mui/icons-material';
import { SalesData, Statistics, TopProduct, OrderStatus } from '../types/dashboard';
import { mockSalesData, mockStatistics, mockTopProducts, mockOrderStatus } from '../utils/mockData';
import TopProductsTable from '@/components/dashboard/TopProductsTable';

export default function Dashboard() {
    const [isLoading, setIsLoading] = useState(true);
    const [salesData, setSalesData] = useState<SalesData>(mockSalesData);
    const [stats, setStats] = useState<Statistics>(mockStatistics);
    const [topProducts, setTopProducts] = useState<TopProduct[]>(mockTopProducts);
    const [orderStatus, setOrderStatus] = useState<OrderStatus[]>(mockOrderStatus);

    const calculateRealTimeStats = () => {
        const orders = JSON.parse(localStorage.getItem('orders') || '[]');
        const products = JSON.parse(localStorage.getItem('products') || '[]');
        const users = JSON.parse(localStorage.getItem('user') || '[]');

        const totalSales = orders.reduce((acc: number, order: any) => acc + order.total, 0);
        
        setStats({
            totalSales: totalSales,
            totalOrders: orders.length,
            totalCustomers: Array.isArray(users) ? users.length : 1,
            totalProducts: products.length
        });
    };

    useEffect(() => {
        const loadData = async () => {
            setIsLoading(true);
            try {
                calculateRealTimeStats();
                
                await new Promise(resolve => setTimeout(resolve, 1000));
                
            } catch (error) {
                console.error('Error loading dashboard data:', error);
                setSalesData(mockSalesData);
                setStats(mockStatistics);
                setTopProducts(mockTopProducts);
                setOrderStatus(mockOrderStatus);
            } finally {
                setIsLoading(false);
            }
        };

        loadData();
    }, []);

    const renderStatCards = () => (
        <Grid container spacing={3} sx={{ mb: 3 }}>
            <Grid item xs={12} sm={6} md={3}>
                {isLoading ? (
                    <Skeleton variant="rectangular" height={160} />
                ) : (
                    <StatCard 
                        title="Total Sales" 
                        value={stats?.totalSales ?? 0}
                        icon={<AttachMoney />}
                        color="primary"
                        trend={12}
                        sx={{
                            background: 'linear-gradient(135deg, #3b82f6 0%, #2563eb 100%)',
                            color: 'white'
                        }}
                    />
                )}
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
                {isLoading ? (
                    <Skeleton variant="rectangular" height={160} />
                ) : (
                    <StatCard 
                        title="Total Orders" 
                        value={stats?.totalOrders ?? 0}
                        icon={<ShoppingCart />}
                        color="secondary"
                        trend={8}
                        sx={{
                            background: 'linear-gradient(135deg, #8b5cf6 0%, #6d28d9 100%)',
                            color: 'white'
                        }}
                    />
                )}
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
                {isLoading ? (
                    <Skeleton variant="rectangular" height={160} />
                ) : (
                    <StatCard 
                        title="Total Customers" 
                        value={stats?.totalCustomers ?? 0}
                        icon={<People />}
                        color="success"
                        trend={15}
                        sx={{
                            background: 'linear-gradient(135deg, #10b981 0%, #059669 100%)',
                            color: 'white'
                        }}
                    />
                )}
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
                {isLoading ? (
                    <Skeleton variant="rectangular" height={160} />
                ) : (
                    <StatCard 
                        title="Total Products" 
                        value={stats?.totalProducts ?? 0}
                        icon={<Inventory />}
                        color="warning"
                        trend={5}
                        sx={{
                            background: 'linear-gradient(135deg, #f59e0b 0%, #d97706 100%)',
                            color: 'white'
                        }}
                    />
                )}
            </Grid>
        </Grid>
    );

    return (
        <DashboardLayout>
            <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
                {renderStatCards()}
                
                <Grid container spacing={3}>
                    <Grid item xs={12} md={8}>
                        <Paper sx={{ p: 3, height: '100%' }}>
                            <Typography variant="h6" gutterBottom>
                                Sales Trend
                            </Typography>
                            {isLoading ? (
                                <Skeleton variant="rectangular" height={300} />
                            ) : (
                                <SalesChart data={salesData} />
                            )}
                        </Paper>
                    </Grid>

                    <Grid item xs={12} md={4}>
                        <Paper sx={{ p: 3, height: '100%' }}>
                            <Typography variant="h6" gutterBottom>
                                Order Status
                            </Typography>
                            {isLoading ? (
                                <Skeleton variant="circular" width={200} height={200} />
                            ) : (
                                <OrderStatusChart data={orderStatus} />
                            )}
                        </Paper>
                    </Grid>

                    <Grid item xs={12}>
                        <Paper sx={{ p: 3 }}>
                            <Typography variant="h6" gutterBottom>
                                Top Products
                            </Typography>
                            {isLoading ? (
                                <Skeleton variant="rectangular" height={200} />
                            ) : (
                                <TopProductsTable products={topProducts} />
                            )}
                        </Paper>
                    </Grid>
                </Grid>
            </Container>
        </DashboardLayout>
    );
} 